.home__image {
    width: 100%;
    position: absolute;
    height: 850px;
}

.home__layoutItemContainer {
    position: relative;
    background-color: white;
    padding: 10px;
    top: 5rem;
    margin: auto;
    width: 75%;
    height: 850px;
    border-radius: 10px;
}

.home__layoutItemContainerHeading {
    text-align: center;
    margin-top: 3rem;
    font-size: 3rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
}

.home__layoutItemContainerPara {
    text-align: center;
}

.home__layoutItemContainerParaSpan {
    color: #660066;
}

.home__layoutItemContainerDiv {
    text-align: center;
    width: 100%;
}

.home__layoutItemContainerTextfield {
    margin: 8%;
    width: 30vw;
    text-align: center;
}

.home__layoutItemContainerBtn {
    cursor: pointer;
    vertical-align: middle;
    color: white;
    background-color: #660066;
    padding: 10px 20px;
    margin: 5px;
    font-size: 18px;
    border: 1px solid white;
    border-radius: 10px;
}

.footer {
    width: 100%;
    bottom: 0;
    left: 0;
    font-weight: 500;
    font-style: italic;
    text-shadow: 0 0 3px #FFf, 0 0 5px #fFF;
    right: 0;
    padding: 10px;
    position: fixed;
    background-color: grey;
    text-align: center;
}

.container {
    display: flex;
    padding: 0;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.section__1 {
    margin-top: 20px;
    flex: 1;
    text-align: center;
}

.section__2 {
    flex: 1;
}

@media (max-width: 900px) {
    .container {
        flex-direction: column;
    }
    .section__2 {
        margin-top: 10%;
    }
}

.section__1 textarea {
    text-align: left;
    padding: 10px;
    font-size: 16px;
}

.home__layoutError {
    text-align: center;
    margin-top: 20px;
}

.home__layoutError>p {
    color: red;
    font-weight: 800;
    display: inline;
    padding: 5px 10px;
    border: 2px solid red;
}